import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StaffPermissionComponent } from '../staff-permission/staff-permission.component';
import { DefaultComponent } from '../../../../default/default.component';
import { RouterModule, Routes } from '@angular/router';
import { LayoutModule } from '../../../../../layouts/layout.module';
import { NgxPaginationModule } from 'ngx-pagination';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';


const routes: Routes = [
    {
        path: "",
        component: DefaultComponent,
        children: [
            {
                path: "",
                component: StaffPermissionComponent
            }
        ]
    },
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes),
        LayoutModule, FormsModule, ReactiveFormsModule, NgxPaginationModule,
    ],
    declarations: [StaffPermissionComponent]
})
export class StaffPermissionModule { }
